<script>
	import ImagePane from './ImagePane.svelte'	

	let images = []
	let maximum = 10
	let interval = 5000

	const nextImage = async () => {
		try {
			const newImage = await fetch('/get')
			images = [await newImage.json(), ...images]

			if(images.length > maximum) {
				images = images.slice(0,maximum)
			}
		} catch (ex) {
			// image amazing ui response here
			console.log(ex)
		}

		setTimeout(nextImage, interval)
	}

	nextImage()
</script>

<div><label for="null">history length</label><input id="maximum-input" bind:value="{maximum}"><label for="null">interval(ms)</label><input id="interval-input" bind:value="{interval}"></div><ImagePane images="{images}"></ImagePane>

<style>input {
  margin-right: 50px; }

:global(html, body) {
  background-color: #53354a; }

